<template>
  <div>
    <div class="container1 fl-ex fl-co ju-ar">
      <div class="pie" ref="chart"></div>
      <div class="sign">
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#ffa336;"></div>
          <p>
            已发布活动:
            <span>5000</span>项
          </p>
          <div style="background-color:#7eb9ff; margin-left:1.08rem;"></div>
          <p>
           计划中活动:
            <span>150</span>项
          </p>
        </div>
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#fe9ea0;"></div>
          <p>
            已开展活动:
            <span>300</span>项
          </p>
          <div style="background-color:#67c259;"></div>
          <p>
            体育活动:
            <span>150</span>项
          </p>
        </div>
        <div class="sign1 fl-ex al-it ju-be">
          <div style="background-color:#fe9166;"></div>
          <p>
            文化活动:
            <span>150</span>项
          </p>
          <div style="background-color:#2ef2e8; margin-left:1rem;"></div>
          <p>
            奖励活动:
            <span>150</span>项
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const echarts = require("echarts");
export default {
  data() {
    return {};
  },
  methods: {
    initCharts() {
      let myChart = echarts.init(this.$refs.chart);
      myChart.setOption({
        tooltip: {
        },
        calculable: true,
        series: [
          {
            color: [
              "#ffa336", //黄色
              "#2ef2e8", //青色
              "#fe9166", //红色
              "#67c259", //绿色
              "#fe9ea0", //粉色
              "#7eb9ff" //蓝色
            ],
            type: "pie",
            radius: [30, 125],
            center: ["50%", "57%"],
            roseType: "radius",
            label: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            lableLine: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            data: [
              { value: 14, name: "已发布活动5000项" },
              { value: 10, name: "奖励活动150项" },
              { value: 10, name: "文化活动150项" },
              { value: 10, name: "体育活动150项" },
              { value: 13, name: "已开展活动300项" },
              { value: 10, name: "计划中活动150项" }
            ]
          }
        ]
      });
    }
  },
  mounted() {
    this.initCharts();
  }
};
</script>

<style scoped>
.container1 {
  width: 100%;
  height: 27rem;
}
.container1 .pie {
  width: 90%;
  height: 19rem;
  margin: 0 auto;
}
.container1 .sign {
  width: 81%;
  height: 4.5rem;
  margin: 0 auto;
}
.container1 .sign .sign1 {
  width: 100%;
  height:1.5rem;
}
.container1 .sign .sign1 > div {
  width: 1.2rem;
  height: 0.5rem;
}
.container1 .sign .sign1 > p {
  font-size: 0.85rem;
  color: black;
  text-align: left;
}
</style>


